@font-face {
  font-family: "Noir Pro";
  src: url("../assets/fonts/NoirPro-Light.ttf") ;
  font-weight: 200;
}
@font-face {
  font-family: "Noir Pro";
  src: url("../assets/fonts/NoirPro-Regular.ttf") ;
  font-weight: 400;
}
@font-face {
  font-family: "Noir Pro";
  src: url("../assets/fonts/NoirPro-Medium.ttf") ;
  font-weight: 600;
}
@font-face {
  font-family: "Noir Pro";
  src: url("../assets/fonts/NoirPro-Bold.ttf") ;
  font-weight: 800;
}
@font-face {
  font-family: "Noir Pro";
  src: url("../assets/fonts/NoirPro-Heavy.ttf") ;
  font-weight: 900;
}
* {
  font-family: "Noir Pro", sans-serif;
}
.heading {
  font-weight: bold !important;
  line-height: 1 !important;
  &--1 {
    font-size: 3.75rem !important;
  }
  &--2 {
    font-size: 3rem !important;
  }
}
.paragraph {
  font-size: 1.875rem !important;
  &--1 {
    font-size: 3rem !important;
    @media screen and (max-width: 996px) {
      font-size: 2.25rem !important;
    }
  }
  &--2 {
    font-size: 20px !important;
  }
}
.text {
  &--white {
    color: #fff !important;
  }
  &--black {
    color: #000 !important;
  }
  &--dark{
    color: #262626 !important;
  }
  &--lh-1-1{
    line-height: 1.1 !important;
  }
  &--lh-1{
    line-height: 1 !important;
  }
  &--light{
    font-weight: 200;
  }
  &--regular{
    font-weight: 400;
  }
  &--medium{
    font-weight: 600;
  }
  &--bold{
    font-weight: 800;
  }
  &--bolder{
    font-weight: 900;
  }
  &--italic{
    font-style: italic;
  }
  &--nomal{
    font-style: normal;
  }
}

