@import "./fonts";
@import "./colors";
.container,
.container-fluid,
.container-sm,
.container-md,
.container-lg,
.container-xl,
.container-xxl {
  width: 100%;
  padding-right: 1rem;
  padding-left: 1rem;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 576px) {
  .container,
  .container-sm {
    max-width: 540px;
  }
}

@media (min-width: 768px) {
  .container,
  .container-sm,
  .container-md {
    max-width: 720px;
  }
}

@media (min-width: 992px) {
  .container,
  .container-sm,
  .container-md,
  .container-lg {
    max-width: 960px;
  }
}

@media (min-width: 1200px) {
  .container,
  .container-sm,
  .container-md,
  .container-lg,
  .container-xl {
    max-width: 1170px;
  }
}

.img-fluid {
  max-width: 100%;
  height: auto;
}
.d-flex {
  display: flex !important;
}
.ant-layout {
  background: $lightgray;
}
// .ant-btn-primary:hover, .ant-btn-primary:focus{

// }
@media screen and (max-width: 576px) {
  .menu-item {
    font-size: 20px !important;
  }
  .ant-btn {
    height: auto;
  }
}
.menu-item {
  font-size: 16px;

  &--white {
    color: $white !important;
    &:hover,
    &:focus {
      color: darken($white, 30%) !important;
      // border-color: darken($white,30%) !important;
    }
  }
}
.ant-btn-primary {
  font-size: 20px;
  height: 45px;
  border-radius: 4px;
  background: $btn;
  font-weight: 600;
  border: none;
  &:hover {
    background: lighten($btn, 10%);
  }
  &:focus {
    background: lighten($btn, 20%);
  }
}

.text-center {
  text-align: center;
}

.check-list {
  li {
    list-style: none;
    position: relative;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    font-size: 20px;
  }
  li:before {
    content: "";
    display: inline-block;
    position: absolute;
    height: 25px;
    width: 25px;
    margin-right: 20px;
    left: -35px;
    top: 2px;
    background-image: url("../assets/check.svg");
  }
}

.download-btn {
  display: inline-flex;
  flex-direction: row;
  background-color: $black;
  border-radius: 5px;
  padding: 5px 10px;
  &__icon{
    max-width: 26px;
    flex: 0 0 26px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__text{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    flex: 0 0 auto;
    // max-width: auto;
    color: $white;
    small{
      font-size: 12px;
      // line-height: 1;
    }
    span{
      font-size: 21px;
      line-height: 1;
    }
  }
  
}
.phone-number {
  font-size: 1.125rem;
  line-height: 1;
}
input.ant-input {
  height: 100%;
  font-size: 20px;
  border-radius: 5px;
  border-color: transparent;
  color: $black !important;
  padding-right: 20px;
  padding-left: 20px;
  height: 45px;
  // margin-bottom: 15px;
  &::placeholder {
    color: #333;
  }
}
.ant-input:focus,
.ant-input-focused {
  color: #333;
}

.belka-section {
  padding-top: 110px;
  padding-bottom: 110px;
}
.background--envelope {
  background: url("../assets/feedback.png") no-repeat center;
}
.background--clouds {
  background: url("../assets/clouds.png") no-repeat center/cover;
}
.background--map {
  background: url("../assets/map-of-world.png") no-repeat;
  background-size: 842px;
  background-position: bottom 35% right;
}
.background--slice {
  background: linear-gradient(
    130deg,
    rgba(255, 255, 255, 1) 47%,
    rgba(207, 233, 245, 1) 47%
  );
}
.background--primary {
  background-color: $primary !important;
  // * {
  //   color: $white !important;
  // }
}
.background--white {
  background-color: $white !important;
  // * {
  //   color: $black !important;
  // }
}

section {
  overflow-x: hidden;
}
@media screen and (max-width: 576px) {
  html,
  body {
    font-size: 12px;
  }
}
.mb-3 {
  margin-bottom: 2rem;
}
@media screen and (max-width: 576px) {
  .items-mobile-center {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
  }
  .download-btn {
    margin-right: 0;
  }
}
.ant-form-item-explain{
  position: absolute;
}
.ant-form-inline .ant-form-item{
  margin-bottom: 24px;
}
.ant-form-inline .ant-form-item {
  margin-right: 0;
}
.form {
  display: flex;
  @media screen and (max-width: 576px) {
    flex-direction: column;
  }
  &__item {
    padding-right: 30px;
    flex: 1;
    @media screen and (max-width: 576px) {
      padding-right: 0;
    }
    &--submit {
      flex: 0;
      padding-right: 0;
      margin-top: 31px;
    }
  }
}
.shadow {
  box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
}
.row-15 {
  margin-left: -15px;
  margin-right: -15px;
}
.logo {
  width: 172px;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 14px;
  float: left;
  margin-left: -29px;
  @media screen and (max-width: 576px) {
    margin-left: 0;
  }
  img {
    max-width: 100%;
    height: auto;
    margin-top: -5px;
  }
}
.card {
  background: $white;
  border-radius: 5px;
  box-shadow: -10px 10px 15px rgba(0, 0, 0, 0.05);
  display: flex !important;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  text-align: center;
  padding: 10px;
  width: 100%;
  &--small {
    padding: 25px;
  }
  &--medium {
    padding: 55px 30px 32px;
    margin: 0 15px 30px;
    max-width: 100%;
    img {
      max-height: 165px;
    }
  }
}

.slider {
  &__item {
    background: $white;

    width: 165px !important;
    min-height: 100%;
    max-height: 100px;

    img {
      max-width: 100%;
      height: auto;
    }
  }
  .slick-list {
    padding-top: 20px;
    padding-bottom: 20px;
    .slick-slide {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
    }
  }
  .slick-track {
    display: flex;
    align-items: stretch;
    justify-content: center;
    .slick-slide {
      height: auto;
      > div {
        height: 100%;
      }
    }
  }
  .slick-arrow {
    background: url("../assets/arrow.svg") no-repeat center/contain !important;
  }
  .slick-prev {
    transform: rotate(180deg);
  }
}
