@import '../../stylesheets/colors';
.Hero1 {
  min-height: 700px;
//   height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
 
  // div.ant-typography, .ant-typography p{
  //     font-size: 3rem;
  // }
  // @media screen and (max-width: 996px){
  //   div.ant-typography, .ant-typography p{
  //       font-size: 2.25rem;
  //   }
  // }
  &__cta {
    position: relative;
    @media screen and (max-width: 996px) {
        // display: flex;
    }
  }
  &__gift {
      margin-bottom: 30px;
    @media screen and (min-width: 996px) {
      position: absolute;
      top: -60px;
      right: 0;
      margin-bottom: 0;
    }

    background: #038fde;
    border-radius: 8px;
    margin-left: auto;

    max-width: 350px;
    text-align: center;
    box-shadow: -10px 10px 15px rgba(0, 0, 0, 0.2);
    &-content {
      padding: 30px 45px 20px;
      // width: 100%;
      // height: 100%;
      // background: #000;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      position: relative;
      overflow: hidden;
      border-radius: inherit;
      &:before,
      &:after {
        content: "";
        display: block;
        position: absolute;
        z-index: 0;
        width: 100%;
        height: 100%;
        background: #2ca1e3;
      }
      &:before {
        top: 0;
        left: -55%;
        transform: rotate(-45deg);
      }
      &:after {
        bottom: -50%;
        right: -65%;
        transform: rotate(-45deg);
      }
      > * {
        z-index: 1;
        position: relative;
      }
    }
  }
}
